.header .burger {
  --burger-width: 30px;
  --burger-height: 20px;
  --burger-line-height: 2px;
  position: relative;
  border: none;
  padding: 0;
  width: var(--burger-width);
  height: var(--burger-height);
  color: var(--gray-white);
  background-color: transparent;
  cursor: pointer;
  position: relative;
  z-index: 100;
  display: none;
}
