// Base
@import "vars";
@import "mixins";
@import "fonts";
@import "settings";

// Header start ========================================================================================================================================================

.header {
  background-color: var(--primary-bs-dark-green);
  // .header__wrap

  &__wrap {
    padding: 24px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media (max-width: 480px){
        padding: 15px 0;
    }
  }

  // .header__logo

  &__logo {
    position: relative;
    z-index: 100;
    svg {
      path {
        transition: all 0.3s ease 0s;
      }
    }
    &:hover {
      svg {
        path.background-logo-bottom {
          fill: #5CB047;
        }
        path.background-logo-top {
          fill: #4C687B;
        }
      }
    }
    @media (max-width: 480px){
        svg {
          width: 132px;
        }
    }
  }

  // .header__nav

  &__nav {
    @media (max-width: 768px){
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      row-gap: 48px;
      padding: 0 15px;
    }
    > ul {
      display: flex;
      align-items: center;
      column-gap: 48px;
      transform: translateX(-35px);
      @media (max-width: 992px){
        column-gap: 25px;
        transform: translateX(0);
      }
      @media (max-width: 768px){
          flex-direction: column;
          row-gap: 15px;
      }
      > li {
        > a {
          font-weight: 400;
          font-size: 14px;
          line-height: 120%;
          text-transform: uppercase;
          color: var(--gray-white);
          position: relative;
          display: inline-block;
          @media (max-width: 768px){
            font-style: italic;
            font-weight: 400;
            font-size: 40px;
            line-height: 110%;
            text-transform: none;
          }
          &:after {
            content: '';
            position: absolute;
            left: 0;
            bottom: -3px;
            transform-origin: 50% 50%;
            transform: scale(0);
            width: 100%;
            height: 1px;
            background-color: var(--gray-white);
            transition: transform 0.3s ease 0s;
          }
          &:hover {
            &:after {
              transform: scaleX(1);
            }
          }
        }
      }
    }
    @media (max-width: 768px){
        position: fixed;
        left: 0;
        top: 0;
        z-index: 99;
        overflow-y: auto;
        width: 100%;
        height: 100vh;
        background-color: var(--primary-bs-dark-green);
        transition: transform 0.3s ease 0s;
        transform: translateX(100%);
        &.menu--active {
          transform: translateX(0%);
        }
        > ul {
          width: 100%;
          height: auto;
      }
    }
  }

  // .header__btn

  &__btn {
    padding: 16px 32px;
    @media (max-width: 480px){
        width: 100%;
        padding: 12px 0;
        text-align: center;
    }
  }

  .burger {
    @include burger;
    position: relative;
    z-index: 100;
    display: none;
    @media (max-width: 768px){
        display: block;
    }
  }
}

// Header end ========================================================================================================================================================

// Hero section start  ========================================================================================================================================================

.hero {
  background-color: var(--primary-bs-dark-green);
  padding-top: 64px;

  // .hero__wrap

  &__wrap {
    display: flex;
    justify-content: space-between;
    margin-bottom: 85px;
    @media (max-width: 768px){
        flex-direction: column;
        row-gap: 30px;
        margin-bottom: 50px;
    }
  }

  // .hero__title

  &__title {
    flex: 1 1 auto;
    font-style: italic;
    font-weight: 400;
    font-size: 96px;
    line-height: 100%;
    color: var(--primary-bs-light-green);
    @media (max-width: 992px){
        font-size: 64px;
    }
    @media (max-width: 768px){
        flex: 0 1 100%;
    }
    @media (max-width: 480px){
        font-size: 48px;
    }
    span {
      font-weight: 600;
      color: var(--gray-white);
      font-style: normal;
      display: inline-block;
    }
  }

  // .hero__box

  &__box {
    flex: 0 1 304px;
    align-self: end;
    @media (max-width: 768px){
      flex: 0 1 100%;
    }
  }

  // .hero__descr

  &__descr {
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: var(--primary-green-200);
    margin-bottom: 24px;
    @media (max-width: 480px){
        font-size: 14px;
    }
  }

  // .hero__btn

  &__btn {
    padding: 16px 80px;
    @media (max-width: 480px){
        width: 100%;
        text-align: center;
    }
  }

  // .hero__video

  &__video {
    position: relative;
    max-width: 1315px;
    margin: 0 auto;
    height: 664px;
    video {
      display: block;
      width: 100%;
      max-width: 100%;
      height: 100%;
      object-fit: cover;
    }
    @media (max-width: 1350px){
      height: auto;
    }
  }

  // .hero__play

  &__play {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    cursor: pointer;
    width: 104px;
    height: 104px;
    border: 2px solid var(--gray-800);
    border-radius: 100%;
    backdrop-filter: blur(10px);
    > img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
    &.hide {
      opacity: 0;
    }
    @media (max-width: 1200px){
      width: 80px;
      height: 80px;
    }
  }
}

// Hero section end ========================================================================================================================================================

// Section vacancies start ========================================================================================================================================================

.vacancies {
  padding-top: 137px;
  padding-bottom: 400px;
  background-position: center bottom;
  background-size: cover;
  background-repeat: no-repeat;
  @media (max-width: 1200px){
    padding-top: 100px;
    padding-bottom: 300px;
  }
  @media (max-width: 992px){
    padding-top: 100px;
    padding-bottom: 200px;
  }
  @media (max-width: 768px){
      padding-top: 80px;
      padding-bottom: 300px;
  }

  // .vacancies__wrap

  &__wrap {
    display: flex;
    justify-content: space-between;
    column-gap: 20px;
    margin-bottom: 32px;
    @media (max-width: 768px){
        flex-direction: column-reverse;
        row-gap: 20px;
    }
  }

  // .vacancies__title

  &__title {
    flex: 1 1 auto;
    font-weight: 400;
    font-size: 56px;
    line-height: 110%;
    color: var(--gray-white);
    span {
      color: var(--primary-bs-light-green);
    }
    @media (max-width: 1200px){
        font-size: 46px;
    }
    @media (max-width: 992px){
        font-size: 40px;
        br {
          display: none;
      }
    }
    @media (max-width: 480px){
        font-size: 32px;
        max-width: 80%;
    }
  }

  // .vacancies__btn

  &__btn {
    display: inline-flex;
    align-self: start;
    flex: 0 1 111px;
    text-align: center;
    font-weight: 400;
    font-size: 14px;
    line-height: 120%;
    text-transform: uppercase;
    color: var(--gray-white);
    padding: 8px 16px;
    @media (max-width: 768px){
        flex: 0 1 100%;
    }
  }

  // .vacancies__text

  &__text {
    max-width: 872px;
    margin-left: auto;
    font-weight: 400;
    font-size: 20px;
    line-height: 140%;
    text-transform: capitalize;
    color: var(--primary-green-200);
    text-indent: 95px;
    margin-bottom: 104px;
    @media (max-width: 992px){
        font-size: 18px;
        margin-bottom: 60px;
        text-indent: initial;
    }
  }

  // .vacancies__image

  &__image {}

  .list-vacancies {
    display: grid;
    grid-template-columns: repeat(2, 640px);
    grid-template-rows: repeat(3, 384px);
    gap: 32px;
    @media (max-width: 1350px){
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(3, auto);
      gap: 15px;
    }
    @media (max-width: 768px){
      grid-template-columns: 1fr;
      max-width: 640px;
      grid-template-rows: repeat(6, 384px);
      grid-auto-rows: 384px;
      margin: 0 auto;
      gap: 24px;
    }

    // .list-vacancies__item

    &__item {
      position: relative;
      max-width: 640px;
      max-height: 384px;
      overflow: hidden;
      .vacancies__image {
        transition: all 0.3s ease 0s;
        display: block;
        @media (max-width: 768px){
            height: 100%;
        }
      }
      &:hover {
        .vacancies__image {
          transform: scale(1.1);
        }
      }
      &:last-child {
        background: var(--primary-bs-light-green);
        padding: 32px;
        display: flex;
        flex-direction: column;
        @media (max-width: 992px){
            padding: 15px;
        }
        @media (max-width: 480px){
            padding: 24px;
        }
        .list-vacancies__wrapper {
          height: 100%;
          display: flex;
          flex-direction: column;
          > img {
            align-self: start;
            justify-self: start;
            margin-bottom: auto;
          }
        }
        .list-vacancies__link {
          position: static;
          display: inline-block;
          font-weight: 500;
          font-size: 16px;
          line-height: 100%;
          padding: 16px 0;
          color: var(--primary-bs-dark-green);
          width: 100%;
          text-align: center;
          margin-top: 48px;
          border: 1px solid var(--primary-bs-dark-green);
          border-radius: 29px;
          @media (max-width: 1200px){
              margin-top: 20px;
          }
          @media (max-width: 991px){
              padding: 10px 0;
          }
          @media (max-width: 480px){
              padding: 16px 0;
              margin-top: 32px;
          }
        }
        .list-vacancies__text {
          margin-top: 16px;
          font-weight: 400;
          font-size: 16px;
          line-height: 150%;
          color: var(--primary-bs-dark-green);
          opacity: 0.6;
          @media (max-width: 1200px){
              margin-top: 12px;
          }
          @media (max-width: 480px){
              font-size: 14px;
              margin-top: 16px;
          }
        }
        .list-vacancies__name {
          font-weight: 500;
          font-size: 32px;
          line-height: 110%;
          text-transform: capitalize;
          color: var(--primary-bs-dark-green);
          @media (max-width: 480px){
              font-size: 20px;
          }
        }
      }
    }

    // .list-vacancies__category

    &__category {
      position: absolute;
      left: 20px;
      top: 32px;
      font-weight: 500;
      font-size: 40px;
      line-height: 110%;
      text-transform: capitalize;
      color: var(--gray-white);
      @media (max-width: 1200px){
          font-size: 30px;
      }
      @media (max-width: 992px){
          left: 15px;
          top: 15px;
      }
      @media (max-width: 768px){
        left: 24px;
        top: 24px;
      }
      @media (max-width: 480px){
          top: 70px;
          left: 24px;
          font-size: 24px;
      }
    }

    // .list-vacancies__tags

    &__tags {
      position: absolute;
      top: 32px;
      right: 32px;
      font-weight: 400;
      font-size: 14px;
      line-height: 100%;
      text-transform: uppercase;
      text-align: center;
      color: var(--primary-bs-svart);
      border-radius: 108px;
      padding: 8px 16px;
      background: var(--gray-white);
      @media (max-width: 1200px){
          font-size: 12px;
          padding: 6px 14px;
      }
      @media (max-width: 992px){
          top: 15px;
          right: 15px;
      }
      @media (max-width: 768px){
        top: 24px;
        right: 24px;
      }
      @media (max-width: 480px){
        right: auto;
        top: 24px;
        left: 24px;
      }
    }

    // .list-vacancies__link

    &__link {
      position: absolute;
      left: 20px;
      bottom: 32px;
      display: flex;
      align-items: center;
      column-gap: 8px;
      font-weight: 400;
      font-size: 14px;
      line-height: 120%;
      text-transform: uppercase;
      color: var(--gray-white);
      img {
        transition: all 0.3s ease 0s;
      }
      &:hover {
        img {
          transform: translateX(5px);
        }
      }
      @media (max-width: 992px){
          left: 15px;
          bottom: 15px;
      }
      @media (max-width: 768px){
        left: 24px;
        bottom: 24px;
      }
      @media (max-width: 480px){
          left: 24px;
          bottom: 24px;
      }
    }
  }
}

// Section vacancies end ========================================================================================================================================================

// Section Om Oss start ========================================================================================================================================================

.om-oss {
  background-color: var(--primary-bs-sand);
  padding: 160px 0;
  @media (max-width: 1200px){
      padding: 100px 0;
  }
  @media (max-width: 992px){
      padding: 80px 0;
  }

  // .om-oss__wrap

  &__wrap {
    display: flex;
    justify-content: space-between;
    column-gap: 30px;
    @media (max-width: 1024px){
        flex-direction: column;
        row-gap: 40px;
    }
    @media (max-width: 480px){
      row-gap: 24px;
    }
  }

  // .om-oss__btn

  &__btn {
    max-width: 87px;
    align-self: start;
    text-align: center;
    font-weight: 400;
    font-size: 14px;
    line-height: 120%;
    text-transform: uppercase;
    text-align: center;
    color: var(--primary-bs-dark-green);
    border: 1px solid var(--primary-bs-dark-green);
    border-radius: 32px;
    padding: 8px 16px;
    &:hover {
      background-color: var(--primary-bs-dark-green);
      color: var(--gray-white);
    }
    @media (max-width: 1024px){
        max-width: auto;
    }
  }

  // .om-oss__content

  &__content {
    max-width: 864px;
    margin-left: auto;
    @media (max-width: 1024px){
        margin-left: 0;
        max-width: none;
    }
  }

  // .om-oss__title

  &__title {
    font-style: italic;
    font-weight: 400;
    font-size: 72px;
    line-height: 110%;
    color: var(--primary-bs-dark-green);
    margin-bottom: 32px;
    span {
      font-style: normal;
      font-weight: 600;
    }
    @media (max-width: 992px){
        font-size: 64px;
    }
    @media (max-width: 768px){
        font-size: 50px;
    }
    @media (max-width: 576px){
        font-size: 40px;
    }
  }

  // .om-oss__block

  &__block {
    display: flex;
    column-gap: 32px;
    margin-bottom: 48px;
    @media (max-width: 480px){
        flex-direction: column;
        row-gap: 16px;
    }
  }

  // .om-oss__text

  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: var(--primary-bs-dark-blue);

    // .om-oss__text_bold

    &_bold {
      font-weight: 600;
      text-transform: capitalize;
    }
  }

  // .om-oss__link

  &__link {
    padding: 16px 80px;
    margin-bottom: 80px;
    &:hover {
      color: var(--primary-bs-dark-green);
    }
    @media (max-width: 992px){
        margin-bottom: 50px;
    }
    @media (max-width: 480px){
        width: 100%;
        text-align: center;
    }
  }

  // .om-oss__list

  &__list {
    display: flex;
    flex-wrap: wrap;
    column-gap: 144px;
    row-gap: 80px;
    @media (max-width: 992px){
        row-gap: 50px;
    }
    @media (max-width: 768px){
        column-gap: 50px;
    }
    @media (max-width: 480px){
        flex-direction: column;
        row-gap: 32px;
    }
  }

  // .om-oss__item

  &__item {
    flex: 0 1 calc(50% - 144px);
    max-width: 304px;
    @media (max-width: 768px){
        flex: 0 1 calc(50% - 25px);
        max-width: none;
    }
    span {
      display: inline-block;
      margin-bottom: 12px;
      font-style: italic;
      font-weight: 400;
      font-size: 96px;
      line-height: 100%;
      color: var(--primary-bs-dark-green);
      @media (max-width: 768px){
          font-size: 72px;
      }
      @media (max-width: 480px){
          font-size: 48px;
      }
    }
    p {
      font-weight: 400;
      font-size: 16px;
      line-height: 150%;
      color: var(--gray-800);
      @media (max-width: 480px){
          font-size: 14px;
      }
    }
  }
}

// Section Om Oss end ========================================================================================================================================================// Section Om Oss end ========================================================================================================================================================

// Section Hjelmen start  ========================================================================================================================================================

