// Section work start ========================================================================================================================================================

.work {
  padding-top: 160px;
  padding-bottom: 403px;
  background-image: url('../img/bg-work.png');
  background-position: center bottom;
  background-repeat: no-repeat;
  background-size: contain;
  background-color: var(--primary-bs-sand);
  @media (max-width: 1200px){
      padding-top: 100px;
      padding-bottom: 300px;
  }
  @media (max-width: 992px){
      padding-bottom: 250px;
  }
  @media (max-width: 768px){
      padding-top: 80px;
      padding-bottom: 150px;
  }

  // .work__wrap

  &__wrap {
    display: flex;
    column-gap: 30px;
    justify-content: space-between;
    @media (max-width: 1200px){
        flex-direction: column;
        row-gap: 50px;
    }
  }

  // .work__content

  &__content {
    flex: 0 1 529px;
    @media (max-width: 1200px){
        flex: 0 1 100%;
        text-align: center;
    }
  }

  // .work__title

  &__title {
    font-style: italic;
    font-weight: 400;
    font-size: 72px;
    line-height: 110%;
    color: var(--primary-bs-dark-green);
    margin-bottom: 32px;
    span {
      font-style: normal;
      font-weight: 600;
    }
    @media (max-width: 992px){
        font-size: 64px;
    }
    @media (max-width: 768px){
        font-size: 50px;
    }
    @media (max-width: 576px){
        font-size: 40px;
    }
  }

  // .work__subtitle

  &__subtitle {
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: var(--primary-bs-medium-blue);
    margin-bottom: 48px;
    @media (max-width: 768px){
        font-size: 14px;
    }
  }

  // .work__link

  &__link {
    padding: 16px 80px;
    &:hover {
      color: var(--primary-bs-dark-green);
    }
  }

  // .work__wrapper

  &__wrapper {
    flex: 0 1 640px;
    max-width: 640px;
    @media (max-width: 1200px){
        flex: 0 1 100%;
        margin: 0 auto;
        width: 640px;
    }
    @media (max-width: 768px){
        width: 100%;
    }
  }

  // .work__slider

  &__slider {
    height: 614px;
    @media (max-width: 1200px){
        height: 384px;
    }
  }

  // .work__slide

  &__slide {
    border: 1px solid var(--primary-bsmedium-blue);
    padding: 40px;
    box-sizing: border-box;
    position: relative;
    > img {
      position: absolute;
      top: 40px;
      left: 40px;
      @media (max-width: 480px){
          width: 30px;
          height: 30px;
      }
    }
    > p {
      font-weight: 400;
      font-size: 16px;
      line-height: 150%;
      color: var(--primary-bs-medium-blue);
      position: absolute;
      bottom: 40px;
      left: 40px;
      right: 40px;
      @media (max-width: 480px){
          font-size: 13px;
      }
    }
  }

  // .work__text

  &__text {}

  .swiper {
    padding-top: 80px;
    padding-bottom: 150px;
    height: 614px;
    @media (max-width: 1200px){
        height: 384px;
        padding-top: 0;
        padding-bottom: 0;
    }
  }

  .swiper-slide {
    max-width: 608px;
    transition: all 0.3s ease 0s;
    margin: 0 auto;
  }

  .swiper-slide-active {
    background: var(--primary-bs-light-green);
    transform: scale(1.1);
    border: none;
  }
}

// Section work end ========================================================================================================================================================

// Section testimonials start ========================================================================================================================================================

.testimonials {
  padding-top: 55px;
  padding-bottom: 403px;
  background-image: url('../img/backgroung-main.png');
  background-position: center bottom;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: var(--primary-bs-sand);
  @media (max-width: 1200px){
    padding-bottom: 300px;
  }
  @media (max-width: 992px){
    padding-bottom: 250px;
  }
  @media (max-width: 768px){
    padding-bottom: 150px;
  }

  // .testimonials__wrap

  &__wrap {
    display: flex;
    justify-content: space-between;
    column-gap: 30px;
    margin-bottom: 160px;
    @media (max-width: 1200px){
        flex-direction: column;
        row-gap: 40px;
        margin-bottom: 80px;
    }
  }

  // .testimonials__btn

  &__btn {
    flex: 0 1 127px;
    align-self: start;
    text-align: center;
    padding: 8px 16px;
    text-transform: uppercase;
    font-weight: 400;
    font-size: 14px;
    line-height: 120%;
    @media (max-width: 1200px){
        flex: 0 1 100%;
    }
  }

  // .testimonials__slider

  &__slider {
    flex: 0 1 864px;
    max-width: 864px;
    margin: 0;
    @media (max-width: 1200px){
        flex: 0 1 100%;
        max-width: none;
    }
  }

  // .testimonials__blockquote

  &__blockquote {
    margin: 0;
    font-weight: 500;
    font-size: 40px;
    line-height: 110%;
    text-transform: capitalize;
    color: var(--gray-white);
    text-indent: 48%;
    margin-bottom: 32px;
    @media (max-width: 992px){
      text-indent: 0;
    }
    @media (max-width: 768px){
        font-size: 30px;
    }
    @media (max-width: 480px){
        font-size: 22px;
        line-height: 130%;
    }
  }

  // .testimonials__author

  &__author {
    font-weight: 600;
    font-size: 16px;
    line-height: 150%;
    color: var(--gray-white);
  }

  // .testimonials__prof

  &__prof {
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: var(--gray-white);
  }

  // .testimonials__image

  &__image {
    width: 100%;
    height: 640px;
    overflow: hidden;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-image: url('../img/gradient.png');
    }
    img {
      max-width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
  }

  .swiper {
    padding-bottom: 125px;
  }

  .swiper-pagination {
    bottom: 20px;
    text-align: left;
  }

  .swiper-pagination-bullet {
    width: 8px;
    height: 8px;
    border-radius: 100%;
    background-color: var(--gray-white);
    opacity: 0.4;
  }

  .swiper-pagination-bullet-active {
    opacity: 1;
  }

  .swiper-button-prev {
    display: none;
  }

  .swiper-button-next {
    display: none;
  }

  .swiper-testimonials-button-prev,
  .swiper-testimonials-button-next {
    z-index: 50;
    cursor: pointer;
    position: absolute;
    width: 56px;
    height: 56px;
    bottom: 0px;
    @media (max-width: 768px){
      svg {
        width: 40px;
        height: 40px;
      }
        width: 40px;
        height: 40px;
    }
    svg {
      rect, path {
        transition: all 0.3s ease 0s;
      }
    }
    &:hover {
      svg {
        rect, path {
          stroke: #BAD862;
        }
      }
    }
  }

  .swiper-testimonials-button-next {
    right: 0;
  }

  .swiper-testimonials-button-prev {
    right: 64px;
    @media (max-width: 768px){
        right: 48px;
    }
  }

  .swiper-button-disabled {
    opacity: 0.4;
    &:hover {
      svg {
        rect, path {
          stroke: #9AAFBB;
        }
      }
    }
  }
}

// Section testimonials end ========================================================================================================================================================

// Footer start ========================================================================================================================================================

.footer {
  background-color: var(--primary-bs-sand);
  padding-top: 160px;
  padding-bottom: 24px;
  @media (max-width: 768px){
      padding-top: 80px;
  }

  // .footer__title

  &__title {
    font-style: italic;
    font-weight: 400;
    font-size: 72px;
    line-height: 110%;
    color: var(--primary-bs-dark-green);
    margin-bottom: 32px;
    max-width: 1100px;
    span {
      font-style: normal;
      font-weight: 600;
    }
    @media (max-width: 992px){
        font-size: 64px;
    }
    @media (max-width: 768px){
        font-size: 50px;
    }
    @media (max-width: 576px){
        font-size: 40px;
    }
  }

  // .footer__btns

  &__btns {
    margin-bottom: 88px;
    a {
      padding: 16px 80px;
      @media (max-width: 768px){
          display: block;
          max-width: max-content;
          margin: 0 auto;
      }
      @media (max-width: 480px){
          max-width: none;
          width: 100%;
          text-align: center;
      }
      &:first-child {
        margin-right: 16px;
        margin-bottom: 16px;
        &:hover {
          color: var(--primary-bs-dark-green);
        }
        @media (max-width: 768px){
            margin-right: auto;
        }
      }
      &:last-child {
        color: var(--primary-bs-dark-green);
        border-color: var(--primary-bs-dark-green);
        &:hover {
          background-color: var(--primary-bs-dark-green);
          color: var(--gray-white);
        }
      }
    }
  }

  // .footer__copy

  &__copy {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media (max-width: 1200px){
        flex-direction: column;
        row-gap: 24px;
    }
    * {
      font-weight: 400;
      font-size: 14px;
      line-height: 150%;
      color: var(--primary-green-700);
    }
    a {
      &:hover {
        text-decoration: underline;
      }
    }
  }

  // .footer__links

  &__links {
    display: flex;
    column-gap: 24px;
    @media (max-width: 576px){
        row-gap: 5px;
        justify-content: center;
        flex-wrap: wrap;
    }
  }

  // .footer__copyright

  &__copyright {
    display: flex;
    column-gap: 24px;
    @media (max-width: 576px){
      flex-direction: column;
      row-gap: 5px;
      align-items: center;
    }
  }
}

// Footer end ========================================================================================================================================================

.hjelmen__line {
  transform-origin: 0 0;
}
